article:nth-child(1) {
  background-color: hsl(31, 77%, 52%);
}

article:nth-child(2) {
  background-color: hsl(184, 100%, 22%);
}

article:nth-child(3) {
  background-color: hsl(179, 100%, 13%);
}

p {
  color: hsla(0, 0%, 100%, 0.75);
}

h2 {
  color: hsl(0, 0%, 95%);
}

main article:nth-child(1) button {
  color: hsl(31, 77%, 52%);
}

main article:nth-child(2) button {
  color: hsl(184, 100%, 22%);
}

main article:nth-child(3) button {
  color: hsl(179, 100%, 13%);
}

@font-face {
  font-family: "Lexend Deca", sans-serif;
  @import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Big Shoulders Display";
  @import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&display=swap");
  font-weight: 700;
  font-style: normal;
}
body {
  font-family: "Lexend Deca";
}

h2 {
  font-family: "Big Shoulders Display";
}

@media (min-width: 800px) {
  html {
    font-size: 17px;
  }
}
*, *::before, *::after {
  margin: 0;
  bottom: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
}

main {
  position: relative;
  overflow: hidden;
  border-radius: 0.3rem;
  width: 12rem;
  transition: all 1s ease;
  animation: interload 1s forwards;
}

article {
  line-height: 1.5;
  font-size: 0.563rem;
  padding: 1.5rem 1.5rem;
}

img {
  width: 2rem;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 1.2rem;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}

button {
  font-size: 0.6rem;
  width: 5.5rem;
  padding: 0.6rem;
  border: none;
  border-radius: 1rem;
  margin-top: 0.8rem;
}

footer {
  position: absolute;
  bottom: 0;
}

@media (min-width: 800px) {
  main {
    width: 40rem;
    display: grid;
    height: 20rem;
    grid-template-columns: 1fr 1fr 1fr;
  }
  article {
    padding: 2rem;
    font-size: 0.75rem;
  }
  button {
    margin-top: 3rem;
  }
  h2 {
    font-size: 1.5rem;
  }
}
@keyframes interload {
  0% {
    transform: translateY(-7rem);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}